<template>
<div is="input-select"
     :large="large"
     @suggest="onSearch"
     :options="institutes"
     :placeholder="placeholder"
     :render-option="renderOption"
     :value.sync="institute"
     :required="true"></div>
</template>

<script lang="babel">
import each from 'lodash/each'
import hasKey from 'lodash/has'
import throttle from 'lodash/throttle'
import InputSelect from 'znck.select'

const history = []
export default {
  props: {
    large: false,
    placeholder: 'Institute'
  },
  components: { InputSelect },
  data() {
    return {
      institute: '',
      institutes: [],
      ids: {}
    }
  },
  methods: {
    renderOption({ o, e, h }) {
      return `<div class="institute-select-option">
                        <img src="${e(o.logo)}" class="institute-select-option-photo" width="48" height="48">
                        <div class="institute-select-option-info">
                            <div class="institute-select-option-name">${h(e(o.name))}</div>
                            <div class="institute-select-option-loc">${e(o.extra.city)}, ${e(o.extra.state)}</div>
                        </div>
                    </div>`
    },
    getInstitutes(q) {
      return this.$http.get('schools', { params: { q } })
              .then((response) => {
                const {data} = response.data
                const institutes = []
                each(data, (institute) => {
                  if (!hasKey(this.ids, institute.id)) {
                    this.ids[institute.id] = true
                    institutes.push(institute)
                  }
                })
                this.institutes = this.institutes.concat(institutes)
              })
              .catch((e) => {
                console.log(e)
              })
    },
    onSearch(event) {
      this.throttle(() => {
        const { value, start, end } = event
        if (history.indexOf(value) === -1) {
          start()
          history.push(value)
          this.getInstitutes(value).then(end).catch(end)
        }
      })
    },
    throttle: throttle((fn) => fn())
  },
  compiled() {
    this.getInstitutes()
  }
}
</script>

<style lang="scss">
@import "../../node_modules/znck.select/dist/bundle";

$text-muted: #b3b3b3 !default;

.institute-select-option {
  display: flex;
}

.institute-select-option-photo {
  margin-right: 1rem;
}

.institute-select-option-info {
  flex: 1;
}

.institute-select-option-name {
  white-space: normal;
}

.institute-select-option-loc {
  color: $text-muted;
  font-size: .75rem;
}
</style>



